import React, { useEffect, useState } from "react";
import { IoIosCloud, IoIosSunny, IoIosRainy } from "react-icons/io";

const Header_introduce = ({ Props }) => {
  const [cityKorean, setCityKorean] = useState("");
  const now = new Date();

  const hours = String(now.getHours()).padStart(2, "0");
  const minutes = String(now.getMinutes()).padStart(2, "0");
  const Sconds = String(now.getSeconds()).padStart(2, "0");

  const nowTime = `( ${hours} : ${minutes} : ${Sconds} )`;
  const changeCity = (city) => {
    Props.cityWeather.setCity(city);
  };

  useEffect(() => {
    if (Props.cityWeather.city === "seoul") setCityKorean("");
    else if (Props.cityWeather.city === "daejeon") setCityKorean("");
    else if (Props.cityWeather.city === "daegu") setCityKorean("");
    else if (Props.cityWeather.city === "busan") setCityKorean("");
    else if (Props.cityWeather.city === "jeju") setCityKorean("");
  }, [Props]);

  const weatherDivision = () => {
    if (Props.cityWeather.weather === "Clouds")
      return (
        <>
          <IoIosCloud /> (  )
        </>
      );
    else if (Props.cityWeather.weather === "Clear")
      return (
        <>
          <IoIosSunny /> (  )
        </>
      );
    else if (Props.cityWeather.weather === "Rain")
      return (
        <>
          <IoIosRainy /> (  )
        </>
      );
  };

  return (
    <div className="introduce">
      <img src="./img/jewan2.jpg" className="Propile" a />
      <h1 className="introduce_text"></h1>
      <h3 className="introduce_text">FrontEnd Developer</h3>
      <img
        onClick={() => window.open("https://github.com/KimJeWan")}
        className="introduce_img pointer"
        src="./img/GH.png"
      ></img>
      <img
        onClick={() => window.open("https://blog.naver.com/wpdhksdl04")}
        className="introduce_img pointer"
        src="./img/naverBlog.png"
      ></img>
      <img
        onClick={() => window.open("https://www.instagram.com/jewangim/")}
        className="introduce_img pointer"
        src="./img/instar.png"
      ></img>
      <div>
        <br />
        {/* <div className="weather_box">
                    {Props.cityWeather.weather === 'err' ? '  ' : 
                        <>
                             <span className="time_text">{nowTime}</span>
                            <br/><span className="city_text">{cityKorean}</span>  <br/><br/>
                            {weatherDivision()}<br/>
                             : {Props.cityWeather.temperature} <br/>
                             : {Props.cityWeather.humidity} %<br/>
                             : {Props.cityWeather.wind} m/s<br/><br/>
                            <span className="another_city_search_text"> </span><br/>
                            <button onClick={()=>changeCity('seoul')}></button>
                            <button onClick={()=>changeCity('daejeon')}></button>
                            <button onClick={()=>changeCity('daegu')}></button>
                            <button onClick={()=>changeCity('busan')}></button>
                            <button onClick={()=>changeCity('jeju')}></button><br/><br/>
                        </>
                    } 
                </div>    */}
      </div>
    </div>
  );
};

export default Header_introduce;
