import RouteControl from "./Router/Route";
import { useEffect, useState } from "react";
import { Config } from "../src/Config/Config";
import axios from "axios";
import "../src/Style/common.css";

function App() {
  const [city, setCity] = useState("daegu");
  const [weather, setWeather] = useState("");
  const [temperature, setTemperature] = useState();
  const [humidity, setHumidity] = useState();
  const [wind, setWind] = useState();

  const [IsLoading, setIsLoading] = useState(false);

  const weatherApiKey = Config.WeathetAPI;

  const Props = {
    cityWeather: {
      city: city,
      setCity: setCity,
      weather: weather,
      temperature: temperature,
      humidity: humidity,
      wind: wind,
    },
  };

  useEffect(() => {
    axios
      .get(
        `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${weatherApiKey}`
      )
      .then((respone) => {
        setWeather(respone.data.weather[0].main); //  (  ,  .. ) 
        setTemperature(Math.floor(respone.data.main.temp) - 273); //  
        setHumidity(respone.data.main.humidity); //  
        setIsLoading(true); //   
        setWind(respone.data.wind.speed);
        console.log(respone);
      })
      .catch((err) => {
        setWeather("err");
        console.log(err);
      });
  }, [city]);

  return (
    <div>
      <RouteControl Props={Props} />
    </div>
  );
}

export default App;
